module.exports = "import produce from 'immer';\n\nexport interface Test {\n  x: number;\n  y: number;\n  z: number;\n}\n\nexport const updateX: (test: Test) => (x: number) => Test = test => x => {\n  return produce(test, draft => {\n    draft.x = x;\n    draft.z = x + draft.y;\n  });\n};\n\nexport const updateY: (test: Test) => (y: number) => Test = test => y => {\n  return produce(test, draft => {\n    draft.y = y;\n    draft.z = draft.x + y;\n  });\n};"